import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PriceListPageComponent } from './price-list.component';

const routes: Routes = [
  {
    path: '',
    component: PriceListPageComponent,
  },
  {
    path: 'form',
    loadChildren: () => import('./form-price-list/form-price-list.module')
      .then(module => module.FormPriceListPagePageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PriceListPageRoutingModule { }
